.types {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.type {
    flex-basis: 48%;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    border-radius: 15px;
    background: #f8f8f8;
    box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1);

    &__left {
        margin-right: 20px;
        flex-basis: 55%;
    }

    &__right {
        flex-basis: 45%;
    }

    &__img {
        box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1);
        background: #fff;
        border-radius: 15px;
        position: relative;
        z-index: 20;

        img {
            padding: 15px;
            display: block;
            width: 100%;
        }
    }

    &__title {
        font-size: 17px;
        text-transform: uppercase;
        padding: 7px 15px;
        background: #e8e8e8;
        border-radius: 0 0 15px 0;
        font-weight: 600;
        margin-bottom: 15px;
        display: inline-block;


        span {
            font-size: 12px;
            opacity: .4;
            display: block;
            margin-bottom: 3px;
        }

    }

    &__size {
        box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.1);
        background: #fff;
        border-radius: 15px;
        margin-left: 10px;
        margin-right: 15px;
        position: relative;
        z-index: 10;
        padding: 20px 15px 10px 15px;
        margin-top: -15px;
        font-size: 13px;
        text-align: center;
    }

    &__btn {
        background: $red;
        color: #fff;
        padding: 13px 10px;
        border-radius: 30px;
        font-weight: 700;
        transition: .3s;
        margin: 15px;
        text-transform: uppercase;
        font-size: 13px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        &:hover {
            background: darken($red, 20%)
        }
    }

    &__temp {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 20px;

    }

    &__icon {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        margin-top: 15px;

        img {
            margin-right: 5px;
        }

    }

    &__price {
        margin-top: 12px;
        margin-right: 15px;
        font-size: 12px;
        font-weight: 700;

        span {
            font-size: 20px;
        }
    }

    &__desc {
        flex-basis: 100%;
        border: 1px solid lightgray;
        border-radius: 0px 50px 50px 0;
        padding: 10px;
        font-weight: 600;
        color: #828282;
        font-size: 15px;
    }
}

@media only screen and (max-width : 1088px) {
    .type {
        flex-basis: 100%;
    }

}


@media only screen and (max-width : 576px) {
    .type {
        &__btn {
            font-size: 8px;
        }

        &__title {
            font-size: 11px;

            span {
                font-size: 9px;
            }
        }

        &__size {
            font-size: 9px;
        }

        &__desc {
            font-size: 9px;
        }

        &__price {

            font-size: 9px;
            margin-bottom: 15px;


            span {
                font-size: 14px;
            }
        }

        &__icon {
            margin-bottom: 5px;
            margin-top: 5px;

        }
    }
}
