.block-heading {
    margin-bottom: 30px;

    p {
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 15px;
        color: $black;
    }

    span {
        color: $red;
        font-size: 18px;
        font-weight: 700;
    }
}

.tizers {
    display: flex;
    flex-direction: column;
}

.tizer {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    &__img {
        margin-right: 15px;
        flex-basis: 20%;

        img {
            width: 100%;
        }
    }

    &__txt {
        flex-basis: 74%;
        font-size: 14px;
        line-height: 1.7;
    }
}

.tizer-cloud {
    padding: 20px;
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 2px 29px 0px rgba(0, 0, 0, 0.16);
    font-size: 14px;
    line-height: 1.7;
    width: 70%;
}

@media only screen and (max-width : 630px) {
    .tizer-cloud {

        width: 100%;
    }


    .block-heading {
        text-align: center;

        p {
            line-height: 1.7;
        }

        span {
            line-height: 1.7;
        }
    }

    .tizer {
        flex-wrap: wrap;

        &__img {
            flex-basis: 60%;
            margin-bottom: 15px;
        }

        &__txt {
            flex-basis: 100%;
        }
    }


}
