.section-osteklenie {
    font-family: "SegoeUI", sans-serif;
    padding: 108px 0;

    &:not(:first-child) {
        padding-top: 0;
    }

    h1,
    h2 {
        text-align: center;
        font-size: 35px;
        font-weight: 700;
        margin-bottom: 45px;


    }
}

@media only screen and (max-width : 768px) {
    .section-osteklenie {
        padding: 50px 0;

        h1,
        h2 {
            font-size: 22px;
            margin-bottom: 30px;
        }
    }

}
