.slider-wrapper {
    display: flex;
    overflow: hidden;
    max-height: 700px;
    margin: 0 250px;
    position: relative;


    .slider-thumb {

        li {
            margin-bottom: 5px;
            margin-right: 0px;
            height: 111px !important;
            width: 111px !important;
            position: relative;
            border-radius: 15px;
            overflow: hidden;
            transition: all .3s;
            opacity: .8;

            &:hover {
                opacity: 1;
            }


            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

        }




    }

    .slider-preview {

        .slick-prev3,
        .slick-next3 {
            border: none;
            border-radius: 50%;
            background: #474747;
            color: #fff;
            width: 50px;
            height: 50px;
            transition: all .3s;
            font-size: 14px;
            cursor: pointer;
            z-index: 40;
            margin: 20px 10px;
            position: absolute;

            &:hover {

                background: $red;
                color: #fff;

            }


        }


        .slick-prev3 {
            left: -7px;
            top: 40%;
        }

        .slick-next3 {
            right: 0px;
            top: 40%;
        }

        li {
            width: 466px;
            height: 348px;
            margin-right: 5px;
            border-radius: 15px;
            overflow: hidden;

            a {
                outline: none;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;


            }
        }


    }

}

@media only screen and (max-width : 1200px) {



    .slider-wrapper {
        display: flex;
        overflow: hidden;
        max-height: 700px;
        margin: 0 100px;
        position: relative;


        .slider-thumb {
            li {
                width: 20%;
                height: auto;
                margin-bottom: 5px;
                margin-right: 0px;


                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }

    .slider-preview {
        li {
            width: 78%;
            height: auto;
            margin-left: 5px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

@media only screen and (max-width: 767px) {
    * {
        min-height: 0;
        min-width: 0;
    }

    .slider-wrapper {
        max-height: 800px;
        flex-direction: column;
        margin: 0 0;


        .slider-thumb {
            max-width: 100%;

            li {
                margin-bottom: 5px;
                margin-right: 5px;
                max-height: 100px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        .slider-preview {
            li {
                padding: 0px;
                margin-left: 0px;
                margin-bottom: 15px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;

                }
            }


        }

    }
}
