.faction {
    display: flex;
    width: 100%;
    border-radius: 15px;
    overflow: hidden;

    form {
        background: #e3ebf8;
        padding: 30px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-basis: 50%;

    }

    &__img {
        background: url('../img/form-action.jpg');
        background-size: cover;
        flex-basis: 50%;

    }

    &__logo {
        margin-bottom: 20px;
    }

    &__subtitle {
        color: $black;
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 20px;
        line-height: 1.3;

        span {
            color: $red;
            background: $yellow;
            padding: 2px;
            border-radius: 5px;
        }
    }

    &__input {
        border-radius: 27px;
        background-color: rgb(242, 242, 242);
        box-shadow: inset 0px 3px 10px 0px rgba(100, 81, 61, 0.18);
        height: 54px;
        border: none;
        width: 70%;
        margin-bottom: 20px;
        padding: 0 20px;
        font-size: 18px;
        font-weight: 700;
    }

    &__btn {
        width: 70%;
        height: 54px;
        margin-bottom: 20px;
        padding: 0 20px;
        font-size: 18px;
        font-weight: 700;
        border: none;
        border-radius: 27px;
        background: $red;
        color: #fff;
        text-transform: uppercase;
        transition: all .3s;
        cursor: pointer;

        &:hover {
            background: darken($red, 20%);
        }



    }
}

@media only screen and (max-width : 1022px) {
    .faction {
        &__img {
            background-position-x: -170px;
        }
    }
}

@media only screen and (max-width : 754px) {
    .faction {
        flex-wrap: wrap;

        form {
            padding: 15px;
            flex-basis: 100%;
            order: 2;

        }

        &__img {
            background: url('../img/form-action.jpg');
            background-size: cover;
            flex-basis: 100%;
            order: 1;
            height: 250px;

        }

        &__btn {
            width: 100%;

        }

        &__input {
            width: 100%;
        }

    }
}
