.about {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &__block {
        flex-basis: 30%;

    }

    &__top {
        border-radius: 20px;
        overflow: hidden;
        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.13);
        margin-bottom: 20px;
    }

    &__image {
        border-bottom: 4px solid #f2f2f2;
        height: 134px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__icon {
        display: flex;
        align-items: center;
    }

    &__title {
        font-size: 16px;
        font-weight: 600;
        text-transform: uppercase;
        color: $red;
        margin-left: 15px;
    }

    &__bottom {
        font-size: 14px;
        line-height: 1.4;
    }
}

@media only screen and (max-width : 652px) {
    .about {
        &__block {
            flex-basis: 100%;
            margin-bottom: 20px;
        }
    }

}
