.cost {
    display: flex;
    justify-content: space-between;

    &__text {
        flex-basis: 48%;
        margin-bottom: 20px;
    }

    &__img {
        flex-basis: 48%;

        img {
            width: 100%;
        }
    }
}

.align-left {
    text-align: left !important;
}

.textbold {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.7;
}

p {
    margin: 10px 0;
    line-height: 1.7;
}

@media only screen and (max-width : 718px) {
    .cost {
        flex-wrap: wrap;

        &__text {
            flex-basis: 100%;
        }

        &__img {
            flex-basis: 100%;
        }
    }
}


.criteria {
    display: flex;
    justify-content: space-between;
    padding: 70px 0;

    &__icons {
        flex-basis: 68%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        h2 {
            width: 55%;
        }

    }

    &__icon {
        flex-basis: 48%;
        display: flex;
        align-items: center;
        margin-bottom: 15px;

        img {
            margin-right: 15px;
        }

        p {
            font-size: 20px;
            font-weight: 700;
        }
    }

    &__form {
        flex-basis: 30%;
        padding: 25px;
        box-shadow: 0px 2px 29px 0px rgba(0, 0, 0, 0.16);
        border-radius: 15px;
        margin-top: 80px;

        input {
            width: 100%;
        }
    }

    &__ftitle {
        font-size: 14px;
        font-weight: 700;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 20px;

        span {
            display: block;
            font-size: 22px;
            color: $red;

        }
    }
}


@media only screen and (max-width : 850px) {
    .criteria {
        flex-wrap: wrap;
        padding-bottom: 20px;

        &__icons {
            flex-basis: 100%;

            h2 {
                width: 100%;
            }
        }

        &__form {
            flex-basis: 100%;
            margin-top: 20px;
        }

        &__icon {
            flex-basis: 100%;


        }

    }
}
