.kinds {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.kind {
    display: flex;
    flex-direction: column;
    flex-basis: 30%;
    border-radius: 15px;
    overflow: hidden;

    &__img {
        overflow: hidden;
        padding: 30px 0;
        background: url('../img/kind.jpg');
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__temp {
        border-radius: 0px 80px 80px 0px;
        color: #fff;
        padding: 10px 5px;
        width: 68px;
        margin-bottom: 10px;
    }

    &__temp--blue,
    &__icon--blue,
        {
        background: #33d8dd;
    }

    &__temp--orange,
    &__icon--orange,
        {
        background: #ff7f1f;
    }

    &__temp--brown,
    &__icon--brown {
        background: #877461;
    }

    &__temp--orange {
        background: #ff7f1f;

    }

    &__ttitle {
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 5px;
    }

    &__tdesc {
        font-size: 9px;
        font-weight: 400;
    }

    &__icon {
        display: flex;
        padding: 5px;
        align-items: center
    }

    &__title {
        font-size: 16px;
        font-weight: 700;
        color: #fff;
        text-transform: uppercase;
    }

    &__bottom {
        background: #f8f8f8;
        padding: 10px;
    }

    &__bottom1 {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__bt {
        font-weight: 700;
        font-size: 14px;
        color: #493d30;
    }

    .btn-red {
        background: $red;
        color: #fff;
        padding: 10px 20px;
        border-radius: 30px;
        font-weight: 700;
        transition: .3s;

        &:hover {
            background: darken($red, 20%)
        }
    }

    hr {
        opacity: .3;
    }

    &__bottom2 {
        display: flex;
        justify-content: space-between;
        padding: 5px 0;
    }

    &__bottom2wrp {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__per {
        color: $red;
        font-weight: 700;
        font-size: 21px;
        margin-bottom: 5px;
    }

    &__perdesc {
        font-size: 9px;
        text-transform: uppercase;
        color: #232323;
    }

    &__bottom3 {
        font-size: 14px;
        line-height: 1.4;
    }

}

@media only screen and (max-width : 900px) {
    .kind {

        flex-basis: 47%;
        margin-bottom: 20px;

    }
}

@media only screen and (max-width : 662px) {
    .kind {

        flex-basis: 100%;

    }
}
