.slidercost {
    margin: 0 -15px;
}

.scitem {
    height: 165px;
    overflow: hidden;
    border-radius: 15px;
    position: relative;
    transition: all .3s;
    margin: 0 15px;

    &:hover img {

        transform: scale(1.3);

    }


    &__img {
        width: 100%;



        &::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            display: block;
            top: 0;
            left: 0;
            background: #00000061;
        }

        img {
            transition: all .5s;

            height: 100%;
        }

    }

    &__title {
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        width: 100%;
        bottom: 20px;
        color: #fff;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 20px;
        display: flex;
        justify-content: center;
        z-index: 40;
    }
}


.arrows2 {
    text-align: center;

    .pr-prev,
    .pr-next {
        border: none;
        border-radius: 50%;
        background: #ececec;
        color: gray;
        width: 50px;
        height: 50px;
        transition: all .3s;
        font-size: 14px;
        cursor: pointer;
        z-index: 30;
        margin: 20px 10px;

        &:hover {

            background: $red;
            color: #fff;

        }

    }

    .pr-prev {
        left: -25px;
        top: 50%;
    }

    .pr-next {
        right: -25px;
        top: 50%;
    }
}
