.fcons {
    display: flex;
    justify-content: space-between;

    &__img {
        position: relative;
        flex-basis: 50%;
    }



    &__logo-wrp {
        position: absolute;
        right: 30px;
        top: 120px;
        font-weight: 700;

        span {
            text-transform: uppercase;
        }
    }
}

.fform {
    flex-basis: 50%;
    padding: 60px 70px;
    box-shadow: 2px 2px 10px #ececec;


    &__title {
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 10px;

    }

    &__subtitle {
        font-size: 25px;
        text-transform: uppercase;
        color: $red;
        margin-bottom: 10px;
        font-weight: 700;
    }

    &__desc {
        font-size: 16px;
        font-weight: 600;
        line-height: 1.7;
        margin-bottom: 20px;
    }

    label {
        display: block;
        color: gray;
        margin-bottom: 10px;
    }

    &__input {
        height: 40px;
        width: 100%;
        border: 1px solid #ececec;
        padding: 7px 20px;
        margin-bottom: 30px;
    }

    &__submit {
        width: 100%;
        padding: 17px 30px;
        background: $red;
        border: none;
        color: #fff;
        font-weight: 600;
        font-size: 18px;
        cursor: pointer;
        transition: all .3s;

        &:hover {
            background: darken($red, 20%);
        }

    }
}

@media only screen and (max-width : 1012px) {
    .fcons {
        &__logo-wrp {
            display: none;
        }
    }


}

@media only screen and (max-width : 726px) {
    .fcons {
        flex-wrap: wrap;

        &__img-wrp {
            img {
                width: 40%;
            }
        }

        &__logo-wrp {
            display: block;
            right: 0;
            top: 0;


        }

        &__img {
            position: relative;
            flex-basis: 100%;

        }
    }



    .fform {
        flex-basis: 100%;
        padding: 15px;
    }


}
