.options-slider {
    margin: 0 -15px;
    position: relative;
}

.option {
    margin: 0 15px;

    &__image {
        height: 165px;
        border-radius: 15px;
        overflow: hidden;
        box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.13);
        position: relative;
        z-index: 20;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__title {
        color: $red;
        background: #e8e8e8;
        text-transform: uppercase;
        font-size: 17px;
        padding: 10px 10px;
        border-radius: 50px;
        margin-top: -30px;
        font-weight: 700;
        width: 100%;
        position: relative;
        z-index: 30;
        margin-bottom: 20px;
    }

    &__text {
        font-size: 13px;
        line-height: 1.7;
    }
}


.arrows {

    .pr-prev,
    .pr-next {
        border: 1px solid $red;
        border-radius: 50%;
        background: #fff;
        color: $red;
        width: 40px;
        height: 40px;
        transition: all .3s;
        font-size: 20px;
        cursor: pointer;
        position: absolute;
        z-index: 30;

        &:hover {
            border: 1px solid $red;
            background: $red;
            color: #fff;

        }

    }

    .pr-prev {
        left: -25px;
        top: 50%;
    }

    .pr-next {
        right: -25px;
        top: 50%;
    }
}

@media only screen and (max-width : 1294px) {
    .arrows {

        .pr-prev,
        .pr-next {
            position: relative;
            margin-bottom: 20px;
        }

        .pr-prev {
            left: 0;

        }

    }
}
