.section-stages {
    background: url('../img/bg2.jpg') no-repeat;
    background-attachment: fixed;
    /*    background-size: cover;*/
    position: relative;
    overflow: hidden;
    padding: 0 !important;
    margin-bottom: 60px;

    &::after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 800px;
        background: #000000cc;
        top: 0;
        left: 0;
        z-index: 10;

    }
}

.stages {
    padding: 60px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 20;

    &__title {
        font-size: 35px;
        font-weight: 700;
        color: #fff;
        flex-basis: 70%;
        line-height: 1.7;
        margin-bottom: 30px;
    }

    &__info {
        flex-basis: 30%;
        color: #fff;
    }

    &__item {
        display: flex;
        margin-bottom: 30px;
    }

    &__day {
        background: $red;
        padding: 5px 7px;
        color: #fff;
        border-radius: 15px;
        font-weight: 600;
        margin-right: 15px;
    }

}

@media only screen and (max-width : 1160px) {
    .stages {
        &__title {
            flex-basis: 50%;
        }

        &__info {
            flex-basis: 50%;
        }
    }
}

@media only screen and (max-width : 754px) {
    .stages {
        flex-wrap: wrap;

        &__title {
            flex-basis: 100%;
            font-size: 22px;
        }

        &__info {
            flex-basis: 100%;
        }
    }
}

@media only screen and (max-width : 380px) {
    .stages {
        &__day {
            flex-basis: 30%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &__text {
            flex-basis: 70%;
        }

    }

}
