.tprice {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 15px;
    margin-bottom: 20px;
}



.trow {
    display: flex;
    margin-bottom: 2px;

}

.tcol-desc {
    flex-basis: 70%;
    background: #f5f5f5;
}

.tcol-price {
    flex-basis: 30%;
    background: #e8e8e8;
}



.tcol {
    padding: 12px 15px;
}

.theadblack {
    background: #292929;
    color: #fff;
}

.theadred {
    background: $red;
    color: #fff;
}

.hidden {
    display: none;
}

.btn__wrp {
    text-align: center;
}

.btn--red {
    background: $red;
    color: #fff;
    padding: 13px 25px;
    transition: all .3s;
    border: 1px solid $red;
    cursor: pointer;
    border-radius: 50px;
    font-size: 16px;
    font-weight: 600;

    &:hover {
        background: #fff;
        color: $red;
    }

    &:focus {
        outline: none;
    }

    &:active {
        outline: none;
    }

}

.tprice--hidden {
    border-radius: 0;
}
