.promo_balcony2 {
    margin-top: 50px;
    background: url('../img/bg1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 40px;
    height: 408px;
    overflow: hidden;

}

.action {
    margin-top: 160px;
    margin-bottom: 40px;
    position: relative;


    &::before {
        content: "";
        display: block;
        position: absolute;
        width: 1220px;
        height: 400px;
        background: #e0211d;
        opacity: 0.8;
        transform: rotate(7deg);
        z-index: 1;
        left: -600px;
        top: -70px;
    }


    &__title {
        font-size: 45px;
        font-family: "SegoeUI", sans-serif;
        color: rgb(255, 255, 255);
        font-weight: bold;
        text-transform: 700;
        margin-bottom: 34px;
        text-transform: uppercase;
        position: relative;
        z-index: 20;
    }

    &__subtitle {
        font-family: "SegoeUI", sans-serif;
        font-size: 28px;
        font-weight: 400;
        line-height: 1.4;
        color: #fff;
        position: relative;
        z-index: 20;


        span {
            font-family: "SegoeUI", sans-serif;
            background: $yellow;
            color: $red;
            padding: 2px;
            border-radius: 5px;
            font-weight: 700;
        }
    }
}

.circle {
    height: 200px;
    width: 200px;
    background: $yellow;
    border-radius: 50%;
    position: absolute;
    z-index: 30;
    top: -100px;
    left: 366px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;

    &__title {
        font-family: "SegoeUI", sans-serif;
        text-transform: uppercase;
        color: #000;
        font-size: 20px;
        margin-bottom: 0px;
        text-align: center;
        font-weight: 700;
        line-height: 1.6;
        text-transform: uppercase;
        margin-bottom: 10px;
    }

    &__price {
        font-family: "SegoeUI", sans-serif;
        font-size: 28px;
        font-weight: 700;
        color: $red;
        margin-bottom: 10px;
    }

    &__subprice {
        font-size: 19px;
        font-family: "SegoeUI", sans-serif;
        font-weight: 600;
        color: $red;

    }
}

@media only screen and (max-width : 610px) {

    .action {
        margin-top: 240px;
        margin-bottom: 0px;

        &::before {
            transform: rotate(0deg);
            z-index: 1;
            height: 200px;
            top: -10px;

        }

        &__title {
            font-size: 35px;
            margin-bottom: 5px;
            text-align: center;
        }

        &__subtitle {
            text-align: center;
        }

    }

    .circle {

        top: -250px;
        left: 15px;
    }

    .promo_balcony2 {
        background-position-x: -1200px;
    }
}
